@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.10.3/font/bootstrap-icons.css");

.form-container {
    max-width: 1000px;
    margin: 10px auto;
    padding: 20px;
    border: 1px solid #e3e3e3;
    border-radius: 5px;
    background-color: #f9f9f9;
}
.target-player-section {
    padding: 15px;
    border: 2px solid #007bff;
    border-radius: 5px;
    background-color: #e7f3ff;
}
.section-heading {
    font-size: 1.25rem;
    margin-top: 20px;
    margin-bottom: 10px;
    font-weight: bold;
}
.submit-btn {
    margin-top: 20px;
}
.tooltip-icon {
    cursor: pointer;
    color: #007bff;
    margin-left: 5px;
}
.tooltip-icon {
    color: gray;
    &:hover {
        color: #555;
    }
}